@import "reset";
@import "variables";
@import "sidebox";
@import "mainbox";
@import url('https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700&display=swap');

body {
  background-color: #EAECED;
  font-family: $primary_font;
  font-size: 15px;
  background-image: url("../public/static/images/triangles-top.svg"), url("../public/static/images/triangles-bottom.svg");
  background-position: left top, right bottom;
  background-repeat: no-repeat; }


h1 {
  font-family: "Roboto",sans-serif;
  font-size: 2rem; }

h3 {
  font-family: "Roboto",sans-serif;
  font-weight: normal;
  font-size: 1.5rem; }

p {
  font-family: $primary_font;
  font-weight: 300;
  color: $tertiary;
  font-size: 16.5px;
  line-height: 1.61803398875; }
b {
  font-weight: bold; }
strong {
  font-weight: bold; }

a {
  text-decoration: none;
  color: $tertiary; }

.site-container {
  margin: 0; }

.grid-item-one {
  height: 100%;
  z-index: 1; }

.mainbox {
  float: right;
  background-color: white;
  border-radius: 20px; }

.sidebox {
  float: left;
  position: fixed;
  text-align: center;
  background-color: white;
  border-radius: 20px;
  min-width: 260px; }


.box-container {
  max-width: 1350px;
  margin: auto; }

/* RESPONSIVE  SECTIONS */
@media only screen and (min-width: 1200px) {
  .box-container {
    max-width: 1250px;
    min-width: 1200px;
    margin: auto;
    padding: 20px; } }

@media only screen and (max-width: 1200px) {
  .box-container {
    display: flex;
    flex-direction: column;
    max-width: 983px;
    min-width: 800px;
    margin: auto;
    padding: 20px; }

  .sidebox {
    float: left;
    position: initial;
    text-align: center;
    background-color: white;
    border-radius: 20px;
    min-width: 260px;
    width: 100%;
    margin-bottom: 20px; } }

@media only screen and (max-width: 850px) {
  .box-container {
    min-width: initial;
    margin: auto;
    padding: 20px; } }


